import React, {useEffect, useState} from 'react';
import {View, StyleSheet, TouchableOpacity, Text} from 'react-native';
import Ionicons from 'react-native-vector-icons/Ionicons';
import PlaySound from './Play';

const {parse} = require('rss-to-json');

const App = () => {
  const [url, setUrl] = useState(false);

  useEffect(() => {
    parse(
      'https://bff.listnr.fm/backend/rssFeed/63b06bd3db429a001c0d4eae/63b06c26299b50001d77bdcd',
    ).then(feed => {
      if (feed.items.length > 0) {
        let list = [];

        for (let i = 0; i < feed.items.length; i++) {
          for (let j = 0; j < feed.items[i].enclosures.length; j++) {
            list.push(feed.items[i].enclosures[j].url);
          }
        }
        setUrl(list);
      }
    });
  }, []);

  return (
    <View style={styles.container}>
      {url ? <PlaySound url={url}></PlaySound> : <Text>Zeel</Text>}
    </View>
  );
};
const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
    backgroundColor: '#000',
  },
  playBtn: {
    padding: 20,
  },
});
export default App;
