import React, {useEffect, useState} from 'react';
import {View, StyleSheet, TouchableHighlight, Text} from 'react-native';
import Ionicons from 'react-native-vector-icons/AntDesign';

var Sound = require('react-native-sound');
Sound.setCategory('Playback');

const App = ({url}) => {
  const [ding, setDing] = useState(null);
  const [isLoad, setIsLoad] = useState(false);
  const [loding, setLoding] = useState(false);
  const [active, setActive] = useState(-1);

  console.log(ding);

  const playPause = (url, activeIndex) => {
    console.log(url, activeIndex);
    if (activeIndex === active) {
      console.log(ding);
      ding.release();
      setActive(-1);
      setDing(null);
      return;
    }
    if (ding) {
      ding.release();
    }
    setLoding(true);
    let _ding = new Sound(url, null, error => {
      if (error) {
        console.log('failed to load the sound', error);
        return;
      }
      console.log('load');
      // if loaded successfully
      console.log(
        'duration in seconds: ' +
          _ding.getDuration() +
          'number of channels: ' +
          _ding.getNumberOfChannels(),
      );
      setIsLoad(!isLoad);
    });
    _ding.setVolume(5);
    setDing(_ding);
    setActive(activeIndex);
  };

  useEffect(() => {
    if (!ding) return;
    ding.play(success => {
      if (success) {
        console.log('successfully finished playing');
      } else {
        console.log('playback failed due to audio decoding errors');
      }
    });
    setLoding(false);
  }, [isLoad]);

  return (
    <View style={styles.container}>
      {url.map((item, index) => {
        return (
          <TouchableHighlight
            style={styles.playBtn}
            onPress={() => playPause(item, index)}>
            <Text
              style={{padding: 10, backgroundColor: 'blue', color: 'white'}}>
              {loding && index == active
                ? `Loading`
                : index != active
                ? `Play ${index + 1}`
                : `Playing ${index + 1}`}
            </Text>
          </TouchableHighlight>
        );
      })}
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
    backgroundColor: '#000',
  },
  playBtn: {
    padding: 20,
  },
});
export default App;
